.footer {
  &__container {
    @extend %container;
    margin-top: 87px; }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 28px;
    border-bottom: 2px solid #E5E5E5; }
  &__list {
    display: flex;
    width: 120px;
    justify-content: space-between;
    @media screen and(min-width: 1280px) {
      margin-right: 60px; } }
  &__language {
    border: none;
    width: 50px;
    justify-items: center;
    grid-area: 1/2/span 1/3;
    justify-self: end;
    @media screen and(min-width: 576px) {
      grid-area: 1/4/span 3/4; }
    @media screen and(min-width: 1280px) {
      top: -100px;
      position: relative; } }
  &__bottom {
    display: grid;
    padding: 26px 0 17px;
    background-color: #FAF9F8;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    align-items: center;
    justify-items: center;
    @media screen and(min-width: 576px) {
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(2, 1fr); } }
  &__bottom-text,
  &__bottom-copy {
    @include text(400, 14px, 17px);
    color: #B3B3B3;
    text-align: center; }
  &__bottom-link {
    margin-right: 17px; }
  &__bottom-payment {
    grid-area: 1/1/span 1/2;
    justify-self: start;
    @media screen and(min-width: 576px) {
      grid-area: 1/1/span 3/2; } }
  &__bottom-block {
    margin-bottom: 20px; }
  &__bottom-wrapper {
    grid-area: 2/1/span 3/3;
    justify-items: center;
    @media screen and(min-width: 576px) {
      grid-area: 1/2/span 2/4;
      width: 320px; } }
  &__bottom-copy {
    margin-top: 10px; }
  &__bottom-text {
    margin-top: 12px; } }
